import React from 'react';
import { connect } from 'react-redux';

import { closePopout, goBack, openModal, openPopout, setPage } from '../../store/router/actions';

import { Div, Panel, Alert, Group, Button, PanelHeader } from "@vkontakte/vkui"

class HomePanelBase extends React.Component {

    state = {
        showImg: false
    };

    showImg = () => {
        this.setState({ showImg: true });
    };

    openPopout() {
        this.props.openPopout(
            <Alert
                actions={[{
                    title: '',
                    autoclose: true,
                    style: 'cancel',
                }, {
                    title: '',
                    autoclose: true,
                    action: this.showImg
                }]}
                onClose={() => this.props.closePopout()}
            >
                <h2> </h2>
                <p>   ?</p>
            </Alert>
        );
    }

    render() {
        const { id, setPage, withoutEpic } = this.props;
        const url = new URL(window.location.href);
        const payload = async () => {
            if (url.searchParams.get('payload') != null) return await url.searchParams.get('payload');
            else {
                return null;
            }
        }

        return (
            <Panel id={id}>
                <PanelHeader>Examples</PanelHeader>
                <Group>
                    {/* <Div>
                        <Button mode="secondary" size="l" stretched={true} onClick={() => setPage('home', 'groups')}> 
                            </Button>
                    </Div> */}
                    <Div>
                        <p>
                            {new URL(window.location.href).searchParams.get('payload') ? (' : ' + new URL(window.location.href).searchParams.get('payload')) : '  '}
                        </p>
                    </Div>
                    {/* <Div>
                        <p>{params.get('payload')}</p>
                    </Div> */}
                    <Div>
                        <Button mode="secondary" size="l" stretched={true} onClick={() => this.openPopout()}> </Button>
                    </Div>
                    <Div>
                        <Button mode="secondary" size="l" stretched={true} onClick={() => this.props.openModal("MODAL_PAGE_BOTS_LIST")}>
                             </Button>
                    </Div>

                </Group>
            </Panel>
        );
    }

}

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal
};

export default connect(null, mapDispatchToProps)(HomePanelBase);
